.text-library-content {
  /* .ant-col-24 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
*/
}
.text-library-content .curriculum-top {
  width: 100%;
  margin-bottom: 20px;
}
.text-library-content .my-curriculum-con .curriculum-tree {
  margin-top: 15px;
  width: 300px;
}
.text-library-content .my-curriculum-con .curriculum-tree-con {
  margin-top: 10px;
  min-height: 500px;
}
.text-library-content .my-curriculum-con .curriculum-list {
  margin: 10px;
  width: 100%;
}
.text-library-content .my-curriculum-con .curriculum-list .anticon {
  margin-right: 8px;
}
.text-library-content .my-curriculum-con .curriculum-list .ant-btn {
  margin-top: 5px;
  width: 100%;
  border-color: #16d555;
}
.text-library-content .my-curriculum-con .curriculum-list .ant-btn i {
  font-size: 20px;
  color: #26954b;
}
.text-library-content .my-curriculum-con .curriculum-list .ant-btn span {
  color: #26954b;
}
.text-library-content .my-curriculum-con .curriculum-recommend {
  margin-top: 15px;
  padding: 0 8px;
  width: 370px;
  border: 1px solid #ebedf0;
}
.text-library-content .my-curriculum-con .curriculum-recommend h3 {
  padding-top: 10px;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
}
.text-library-content .my-curriculum-con .curriculum-recommend .curriculum-con {
  padding: 10px;
  border-bottom: 1px solid #ebedf0;
}
.text-library-content .my-curriculum-con .curriculum-recommend .curriculum-con .anticon {
  margin-right: 5px;
}
.text-library-content .my-curriculum-con .curriculum-recommend .curriculum-con .curriculum-name {
  margin: 5px 0;
  line-height: 20px;
}
.text-library-content .my-curriculum-con .curriculum-recommend .curriculum-con span {
  margin: 5px 0;
  font-size: 12px;
}
.text-library-content .my-curriculum-con .curriculum-recommend .curriculum-con .usage-amount {
  display: block;
  width: 100%;
  text-align: right;
}
.text-library-content .my-curriculum-con .curriculum-recommend .curriculum-con:last-child {
  border-bottom: none;
}
.text-library-content .paper-tree {
  min-height: 500px;
  border: 1px solid #dee6ee;
}
.text-library-content .paper-year {
  color: #000;
  font-weight: bold;
  display: inline-block;
  margin-bottom: 0 !important;
  text-align: right;
}
.text-library-content .paper-all {
  text-align: right;
  /*padding: 20px 15px;*/
  color: #000;
  font-size: 14px;
  font-weight: 300;
  height: 38px;
  line-height: 38px;
}
.text-library-content .paper-all .paper-number {
  color: #27CDA2;
  display: inline-block;
  margin: 0 5px;
}
.text-library-content .paper-all .ant-switch {
  margin-left: 15px;
}
.text-library-content .paper-all .ant-switch.ant-switch-checked {
  background-color: #27CDA2;
}
.text-library-content .paper-right-top .paper-common .ant-btn {
  margin: 0 5px 10px;
  padding: 0 5px;
  min-width: 42px;
}
.text-library-content .tree-left {
  background: #ffffff;
  box-shadow: 2px 8px 16px -8px rgba(29, 32, 31, 0.12);
  border-radius: 5px;
  padding: 20px 10px;
  min-height: 500px;
}
.text-library-content .tree-left .tree-all-btn {
  width: 100%;
  line-height: 30px;
  height: 30px;
  background: rgba(39, 205, 162, 0.1);
  margin: 20px 0 0;
  padding: 4px 0;
  border-radius: 16px;
}
.text-library-content .tree-left .tree-all-btn .ant-tree-title {
  color: #27CDA2;
  font-size: 14px;
}
.text-library-content .tree-left .ant-select {
  width: 100%;
}
.text-library-content .tree-left .ant-select-selection {
  border-radius: 20px;
  border: none;
  background: #f8f8f8;
}
.text-library-content .tree-left .ant-select-arrow .ant-select-arrow-icon svg {
  color: #27CDA2;
}
.text-library-content .paper-right-menu {
  background: #ffffff !important;
  border-radius: 20px;
  border: 4px solid #ffffff;
  padding: 15px;
}
.text-library-content .paper-right-menu .paper-common .paper-common-btn .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
}
.text-library-content .paper-right-menu .paper-common .paper-common-btn .ant-btn.ant-btn-primary {
  border-radius: 5px !important;
  height: 24px;
  line-height: 24px;
}
.text-library-content .paper-right-menu .paper-common .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.text-library-content .paper-right-menu .paper-common .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.text-library-content .paper-right-menu .paper-common .ant-radio-button-wrapper {
  margin: 0 10px 5px 0;
  color: #000;
  background: none;
  border: none;
}
.text-library-content .paper-right-menu .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.text-library-content .paper-right-menu .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #27CDA2;
  border-radius: 5px;
  height: 24px;
  line-height: 24px;
  color: #fff;
}
.text-library-content .paper-right-menu .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.text-library-content .ant-btn-link {
  color: #000 !important;
}
.text-library-content .ant-btn span {
  font-size: 13px;
}
.text-library-content .paper-title {
  color: #000;
  font-size: 22px;
}
.text-library-content .ant-card {
  border: 1px solid #f3f3f3;
  margin-bottom: 10px;
  border-radius: 5px;
}
.text-library-content .ant-card-body {
  padding: 0 0;
  zoom: 1;
  line-height: 3;
}
.text-library-content .paper-right-bottom {
  min-height: calc(100vh - 200px);
  padding: 10px;
  box-shadow: 2px 8px 16px -8px rgba(29, 32, 31, 0.12);
  border-radius: 5px;
  background: #fff;
}
.text-library-content .paper-right-bottom .question-select {
  padding: 0 15px;
  text-align: center;
}
.text-library-content .paper-right-bottom .ant-card-head {
  border-bottom: none;
}
.text-library-content .paper-right-bottom .ant-card-head .ant-card-head-title {
  cursor: pointer;
}
.text-library-content .paper-right-bottom p {
  display: inline-block;
  margin-bottom: 0 !important;
}
.text-library-content .create-button {
  font-size: 15px;
}
.text-library-content .tab-wrap {
  margin: 8px 30px;
}
.text-library-content .tab-wrap .tab {
  width: 400px;
  height: 44px;
  display: flex;
  border: 1px solid #999;
  background: #fff;
  border-radius: 5px 0px 0px 5px;
}
.text-library-content .tab-wrap .tab span {
  width: 50%;
  display: block;
  line-height: 44px;
  text-align: center;
  cursor: pointer;
}
.text-library-content .tab-wrap .tab span:first-child {
  border-right: 1px solid #999;
}
.text-library-content .question-tab {
  background: #ffffff;
  box-shadow: 2px 8px 16px -8px rgba(29, 32, 31, 0.12);
  border-radius: 20px;
  padding: 5px;
}
.text-library-content .question-tab .ant-tabs-bar {
  margin-bottom: 5px;
}
.text-library-content .question-tab .ant-tabs-nav {
  width: 100%;
  height: 32px;
  line-height: 32px;
  background: #F5F7F7;
  border-radius: 16px;
}
.text-library-content .question-tab .ant-tabs-nav .ant-tabs-tab-active {
  background: #27CDA2;
  border-radius: 15px;
  color: #fff;
  font-weight: bold;
  height: 100%;
}
.text-library-content .question-tab .ant-tabs-tab {
  color: #000;
  font-size: 12px;
  margin: 0 0;
  width: 33.3%;
  text-align: center;
  padding: 0;
  border-bottom: none;
}
.text-library-content .question-tab .question-tab-item span {
  height: 44px;
  line-height: 44px;
  font-size: 16px;
}
.text-library-content .question-tab .question-item {
  text-align: center;
  height: 38px;
  line-height: 38px;
  cursor: pointer;
  font-size: 12px;
  color: #000;
}
.text-library-content .question-tab .question-item .isActive {
  color: #27CDA2;
  cursor: pointer;
  font-weight: bold;
  display: inline-block;
  padding: 0 15px;
  border-bottom: 2px solid #27CDA2;
}
.text-library-content .question-tab .left-paper-btn {
  width: 180px;
  height: 32px;
  line-height: 32px;
  background: #27CDA2 !important;
  border-radius: 16px !important;
  border: 1px solid #27cda2;
  margin-bottom: 10px;
}
.text-library-content .question-tab .question-teaching-list {
  margin: 17px 0 50px;
  width: 100%;
}
.text-library-content .question-tab .question-teaching-list .ant-select {
  width: 100%;
}
.text-library-content .question-tab .question-teaching-list .ant-select-selection {
  width: 100%;
  height: 32px;
  background: #F5F7F7;
  border-radius: 16px;
  line-height: 32px;
  color: #2F3130;
  font-size: 14px;
  font-weight: 400;
}
.text-library-content .question-tab .question-teaching-list .ant-select-selection i {
  color: #27CDA2;
}
.text-library-content .ant-tabs-nav {
  width: 100%;
}
.text-library-content .question-search {
  width: 45%;
  margin-bottom: 20px;
}
.text-library-content .question-select p {
  margin-bottom: 10px;
}
.text-library-content .question-container {
  background: #f1f1f1;
  padding: 0 15px;
  color: #6B706F;
  font-size: 12px;
  height: 36px;
  line-height: 16px;
}
.text-library-content .question-container .question-bottom {
  display: flex;
  position: relative;
}
.text-library-content .question-container .question-bottom .user-img {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  position: absolute;
  bottom: 5px;
  left: 0;
}
.text-library-content .question-container .question-bottom .question-bottom-left .user-name {
  color: #6B706F;
  font-size: 12px;
  margin-bottom: 0;
  height: 16px;
  line-height: 16px;
}
.text-library-content .question-container .question-bottom .question-bottom-left .user-name span {
  font-weight: bold;
  margin-right: 5px;
}
.text-library-content .question-container .question-bottom .question-bottom-left .question-bottom-doc span {
  margin-right: 10px;
}
.text-library-content .question-container .question-bottom-right {
  color: #DE923F;
  font-size: 12px;
  text-align: right;
  line-height: 36px;
}
.text-library-content .question-container .question-bottom-right span {
  padding: 0 7px;
  cursor: pointer;
}
.text-library-content .similar-add {
  margin: 15px 0;
}
.text-library-content .gap-item {
  min-width: 50px;
  border-bottom: 1px solid #ddd;
  margin-left: 10px;
}
.text-library-content .create-course-title {
  height: 40px;
  line-height: 40px;
  width: 80px;
  text-align: left;
}
.text-library-content .analyze-wrap .analyze-error {
  text-align: right;
  color: #448cff;
  cursor: pointer;
}
.text-library-content .analyze-wrap .ant-card-head-title {
  white-space: pre-wrap;
}
.text-library-content .analyze-wrap .testing {
  margin-bottom: 15px;
}
.text-library-content .analyze-wrap .testing span {
  color: #448cff;
  margin-right: 10px;
}
.text-library-content .analyze-wrap .answer {
  margin-bottom: 15px;
}
.text-library-content .analyze-wrap .answer span {
  margin-right: 10px;
}
.text-library-content .analyze-wrap .ant-row-flex {
  margin: 20px 0;
}
.text-library-content .analyze-wrap .add-test {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.text-library-content .retrieval-wrap {
  margin: 0px 0 15px;
}
.text-library-content .retrieval-wrap .ant-radio-group-solid .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  background: #27CDA2;
  color: #fff;
  border-radius: 20px ;
}
.text-library-content .retrieval-wrap .ant-radio-button-wrapper {
  border: none;
  background: none;
  margin-right: 20px;
}
.text-library-content .retrieval-wrap .ant-radio-button-wrapper:not(:first-child)::before {
  position: absolute;
  top: 0;
  left: -1px;
  display: block;
  width: 0px;
  height: 100%;
  content: '';
}
.my-collect-tree .ant-tree-treenode-selected .ant-tree-node-selected {
  background: transparent!important;
  color: #2CA7EC !important;
}
.my-collect-tree .ant-tree-node-content-wrapper:hover {
  /*background-color: #f5f5f5 !important;*/
  background: transparent!important;
  color: #2CA7EC !important;
}
.my-collect-tree .ant-tree-node-selected:hover {
  /* background-color: #eee !important;*/
  background: transparent!important;
  color: #2CA7EC !important;
}
